import {Collapse} from 'antd';
import {useSelector} from "react-redux";

const Item = () => {

    const {data} = useSelector(state => state.duplicatesSlice)
    const sorted_duplicates = data.duplicates?.slice().sort(
        (a, b) => b.duplicates_count - a.duplicates_count
    );
    const {Panel} = Collapse;

    return (
        <Collapse className={'list'} defaultActiveKey={['1']}>
            {sorted_duplicates?.map((item, index) =>
                <Panel className={'item'} header={
                    <>
                        <h1>{item.filename}</h1>
                        <h2>{item.hash}</h2>
                        <div className={'count'}>{item.duplicates_count}</div>
                    </>
                } key={index + 1}>
                    {item.files.map((file, file_index) =>
                        <p className={'paths'} key={file_index}>{file}</p>
                    )}
                </Panel>
            )}
        </Collapse>
    );
};

export default Item;
