import axios from "axios";
import { getData } from "./duplicSlice";
import { changeLoading } from "../loading/loadingSlice";

// const currentProtocol = window.location.protocol;
// const currentDomain = window.location.hostname;
// const currentPort = window.location.port;
//
// const baseUrl = (currentProtocol + '//' + currentDomain + ':' + currentPort);

// const url = `${baseUrl}/find-duplicates`;

const url = "http://127.0.0.1:8080/find-duplicates"

export const getDataAxios = (data) => async (dispatch) => {

    const error = document.getElementById('error');

    try {
        const response = await axios.post(
            url,
            data
        );
        dispatch(getData(response.data));
        dispatch(changeLoading(false))
        error.style.display = 'none';
    } catch (e) {
        error.innerText = e.response.data;
        error.style.display = 'block';
        dispatch(changeLoading(false))
    }
};
