import {combineReducers, configureStore} from "@reduxjs/toolkit";
import duplicatesSlice from "./reducer/duplicates/duplicSlice";
import loadingSlice from "./reducer/loading/loadingSlice";


const rootReducer = combineReducers({
    duplicatesSlice,
    loadingSlice
});

export const setupStore = () => {
    return configureStore({
        reducer: rootReducer
    });
};
