import {Progress, Space} from 'antd';
import {useSelector} from "react-redux";

const Circle = () => {

    const { data } = useSelector(state => state.duplicatesSlice)

    return (
        <div className={'circle'}>
            <Space wrap>
                <Progress
                    strokeColor={'#ff0000'}
                    type="circle"
                    percent={data.duplicates_percentage
                        ?data.duplicates_percentage
                        : 0}
                    format={
                        (percent) => `${data.total_duplicates_count
                            ?data.total_duplicates_count
                            :0}/${data.files_count
                            ?data.files_count
                            :0}`
                    }
                />
            </Space>
        </div>
    );
};

export default Circle;
