import {useDispatch, useSelector} from "react-redux";
import { Input } from 'antd';
import {getDataAxios} from "../store/reducer/duplicates/axios";
import {changeLoading} from "../store/reducer/loading/loadingSlice";

const Details = () => {

    const { data } = useSelector(state => state.duplicatesSlice)

    const dispatch = useDispatch();

    const getPath = (data) => {
        dispatch(getDataAxios({path_to_folder: data}));
    }
    const setLoading = (data) => {
        dispatch(changeLoading(data));
    }

    return (
        <div className={'details'}>
            <Input
                className={'path-input'}
                placeholder="path: C:\"
                onPressEnter={(e) => {
                    getPath(e.target.value);
                    setLoading(true);
                }}
            />
            <p id={'error'}></p>
            <hr style={{height: '1px', width: "100%"}}/>
            <p className={'text'}>Total files in defined path: {data.files_count}</p>
            <p className={'text'}>Total duplicate files: {data.total_duplicates_count}</p>
            <p className={'text'}>
                Percentage of duplicates: {
                    data.duplicates_percentage?.toFixed(2) ?
                    data.duplicates_percentage?.toFixed(2) :
                    0
                } %
            </p>
            <hr style={{height: '1px', width: "100%"}}/>
            <p className={'text'}>Search path: {data.search_path}</p>
            <p className={'text'}>
                Spent time: {
                    data.spent_time?.toFixed(2) ?
                    data.spent_time?.toFixed(2) :
                    0
                } seconds</p>
        </div>
    );
};

export default Details;