import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    data: [],
};

export const duplicatesSlice = createSlice({
    name: 'duplicates',
    initialState,
    reducers: {
        getData(state, action) {
            state.data = action.payload;
        },
    },
});

export const {
    getData
} = duplicatesSlice.actions;

export default duplicatesSlice.reducer;
